import React from 'react'
import ReactGA from 'react-ga'
import PropTypes from 'prop-types'

import Button from '@material-ui/core/Button'
import Icon from '@material-ui/core/Icon'
import Snackbar from '@material-ui/core/Snackbar'
import CloseIcon from '@material-ui/icons/Close'
import CheckIcon from '@material-ui/icons/Check'

const setCookie = gaProp => {
  ReactGA.initialize(gaProp)
  ReactGA.pageview(window.location.pathname + window.location.search)
}

const GaSnackbar = ({ gaProperty, message, labels }) => {
  const consentCookie = 'cu-consent'
  const [openSnackbar, setOpenSnackbar] = React.useState(false)

  React.useEffect(() => {
    const cookieValue = checkCookie(consentCookie)
    // Only show the snackbar when the cookie is not set
    if (cookieValue === null) {
      setOpenSnackbar(true)
    }

    if (cookieValue === true) {
      setCookie(gaProperty)
    }
  }, [gaProperty])

  const handleOptOut = () => {
    document.cookie = consentCookie + '=false'
    setOpenSnackbar(false)
  }

  const handleConsent = () => {
    document.cookie = consentCookie + '=true'
    setOpenSnackbar(false)
    setCookie(gaProperty)
  }

  const checkCookie = cookieName => {
    const cookies = document.cookie.split(';')
    let cookieValue = null

    const _cookie = cookies.filter(cookieKey => {
      let output = ''

      if (cookieKey.split('=')[0] === cookieName) {
        output = cookieKey
      }
      return output
    })

    if (_cookie.length > 0) {
      cookieValue = _cookie[0].split('=')[1] === 'true'
    }

    return cookieValue
  }

  return (
    <Snackbar
      anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      key={`gao-message-id`}
      open={openSnackbar}
      ContentProps={{
        'aria-describedby': 'consent-snackbar',
      }}
      aria-describedby="Google Analytics Opt Out"
      message={
        <span id="consent-snackbar" style={{ textTransform: 'uppercase' }}>
          <Icon className={'Info'} />
          {message}
        </span>
      }
      action={[
        <Button
          key={'close'}
          aria-label={labels.consent}
          color={'primary'}
          onClick={handleConsent}
        >
          {labels.consent}
          <CheckIcon />
        </Button>,
        <Button
          key={'opt-out'}
          aria-label={labels.optOut}
          color={'primary'}
          onClick={handleOptOut}
        >
          {labels.optOut}
          <CloseIcon />
        </Button>,
      ]}
    />
  )
}

GaSnackbar.propTypes = {
  gaProperty: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  labels: PropTypes.object,
}

export default GaSnackbar
